
import http from 'js/http'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {colorTheme, colorSubTheme} from 'js/constant'
import {mapState, mapMutations, mapGetters} from 'vuex'
import slide from 'components/slide/bottom'
import {GetUrlParam} from 'js/mixin'

const imgCodeType = 'UserLogin'

export default {
name: "login",
components: {
XInput,
XButton,
Group,
Cell,
slide
},
data() {
return {
loginForm: {
mobile: '',
vcode: '',
captcha_code: '',
re_code: ''
},
colorTheme: colorTheme,
colorSubTheme: colorSubTheme,
imgCode: {
imageData: '',
imageCode: ''
},
wait: 0,
reCodeShow: false,
btnLoading: false,

}
},
methods: {
backPath() {

this.$router.back()
/*console.log(window.history.length)
if (window.history.length <= 1) {
this.$router.replace({path:'/'})
return false
} else {
this.$router.go(-1)
}*/
},
existMobile() {
let clickMb = this.clickMobile(this.loginForm.mobile)
if (clickMb) {
this.$vux.toast.show(clickMb)
return
}
http.post('/validate/user/exist-mobile', {mobile: this.loginForm.mobile}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.reCodeShow = !res.content
})
},

submitLogin() {
let clickMb = this.clickMobile(this.loginForm.mobile)
if (clickMb) {
this.$vux.toast.show(clickMb)
return
}
if (!this.loginForm.captcha_code) {
this.$vux.toast.show('请输入图片验证码')
return
}
if (!this.loginForm.vcode) {
this.$vux.toast.show('请输入手机验证码')
return
}
/*if (this.reCodeShow) {
if (!this.loginForm.wallet_address) {
this.$vux.toast.show('请输入钱包地址')
return
}
if (this.loginForm.wallet_address.length !== 26) {
this.$vux.toast.show({
text: '请输入有效的钱包地址',
width: '12em'
})
return
}
}*/
this.getLogin()
},
getLogin() {
this.btnLoading = true
http.post('/login', Object.assign({
image_code: this.imgCode.imageCode,
type: imgCodeType
}, this.loginForm), (res) => {
this.$vux.toast.show(res.msg)
this.btnLoading = false
this.getImgCode()
if (res.code === 0) {
localStorage.setItem("loginMsg", JSON.stringify(res.content));
this.setLoginMsg(res.content)
for (let item in this.loginForm) {
this.loginForm[item] = ''
}
this.existPayPsw()

}
})
},
existPayPsw() {
http.post('/pay/exist-pass', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
let n = res.content ? '1' : '0'
this.setPayPsw(n)
localStorage.setItem('payPsw', n)
if (res.content) {//已有支付密码
setTimeout(() => {
this.$router.back()
}, 1000)
} else {//去设置
this.$router.replace({
path: '/setpsw'
})
}
})
},
clickMobile(mobile) {
if (!mobile) {
return '请输入手机号'
}
if (!(/^1\d{10}$/.test(mobile))) {
return '请输入有效的手机号'
}
return ''
},
clickCodeBtn() {
let clickMb = this.clickMobile(this.loginForm.mobile)
if (clickMb) {
this.$vux.toast.show(clickMb)
return
}
if (this.wait <= 0) {
this.getCode()
}
},
getCode() {
this.wait = 60
let itv = setInterval(() => {
if (this.wait) {
this.wait--
} else {
clearInterval(itv)
}
}, 1000)

http.post('/sms/sms/user-login', {mobile: this.loginForm.mobile}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
}
})
},
getImgCode() {
http.post('/captcha/captcha/show', {type: imgCodeType}, (res) => {
this.imgCode = res.content
})
},
...mapMutations({
setLoginMsg: 'LOGIN_MSG',
setPayPsw: 'PAY_PSW'
})
},
computed: {
codeStr() {
if (!this.wait) {
return '获取验证码'
} else {
return `重新获取(${this.wait}s)`
}
}
},
created() {

let pathCode = GetUrlParam('re_code')

if (pathCode) {
this.loginForm.re_code = pathCode
this.reCodeShow = true
}
this.getImgCode()
}
}



